.intro-bg {
  width: 100vw;
  height: 100vh;
  background-color: rgb(55, 55, 55);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.intro-title {
  font-size: 40px;
  font-family: "Arapey", serif;
  height: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: teal; */
}
.intro-body-container {
  height: 80%;
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: "Josefin Sans", sans-serif;
  font-size: 20px;
  /* background-color: tomato; */
}
.intro-space {
  height: 10%;
}
.intro-body {
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-family: "Josefin Sans", sans-serif;
  font-size: 20px;
  /* background-color: thistle; */
}
.intro-name {
  font-size: 25px;
}
.intro-picture {
  display: inline-block;
  position: relative;
  width: 15vw;
  min-width: 150px;
  height: 15vw;
  min-height: 150px;
  overflow: hidden;
  border-radius: 100%;
}
.intro-arrowdown > img {
  width: 30px;
}

.intro-picture img {
  width: 100%;
  /* margin-left:-50%; */
}
@media screen and (max-width: 768px) {
  .intro-body-container {
    width: 75%;
  }
  .intro-space {
    height: 0;
  }
  .intro-body {
    height: 80%;
  }
}
@media screen and (max-width: 568px) {
  .intro-body {
    height: 93%;
  }
}
